:root {
  --font-main: "Lexend Deca", sans-serif;
  --font-header: "Big Shoulders Display", cursive;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --bright-orange: hsl(31, 77%, 52%);
  --dark-cyan: hsl(184, 100%, 22%);
  --very-dark-cyan: hsl(179, 100%, 13%);
  --trans-white-paragraph: hsla(0, 0%, 100%, 0.75);
  --very-ltgray: hsl(0, 0%, 95%);
}

body {
  background-color: var(--very-ltgray);
}

.sedans {
  background-color: var(--bright-orange);
}

.suvs {
  background-color: var(--dark-cyan);
}

.luxury {
  background-color: var(--very-dark-cyan);
}

.btn {
  background-color: var(--very-ltgray);
  border: rem(2) solid var(--very-ltgray);
}

.sedans > .btn {
  color: var(--bright-orange);
}

.sedans > .btn:hover {
  background-color: var(--bright-orange);
  color: var(--white);
}

.suvs > .btn {
  color: var(--dark-cyan);
}

.suvs > .btn:hover {
  background-color: var(--dark-cyan);
  color: var(--white);
}

.luxury > .btn {
  color: var(--very-dark-cyan);
}

.luxury > .btn:hover {
  background-color: var(--very-dark-cyan);
  color: var(--white);
}

html > * {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h2 {
  font-family: var(--font-header);
  font-size: 2.0625rem;
  color: var(--very-ltgray);
  text-transform: uppercase;
  letter-spacing: 0.05rem;
}

p {
  font-family: var(--font-main);
  font-size: 0.9375rem;
  color: var(--trans-white-paragraph);
  line-height: 1.6;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.btn {
  font-weight: bold;
}

h2 {
  margin: 2.5rem 0 2.5rem;
}
@media (max-width: 60em) {
  h2 {
    margin: 2.1875rem 0 2.1875rem;
  }
}

p {
  margin-bottom: 5.4375rem;
}
@media (max-width: 60em) {
  p {
    margin-bottom: 1.875rem;
  }
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  max-width: 57.5rem;
}

.cards {
  display: flex;
  border-radius: 0.625rem;
  overflow: hidden;
  max-height: 31.25rem;
}
@media (max-width: 60em) {
  .cards {
    flex-direction: column;
    max-height: max-content;
    margin-top: 5.5rem;
  }
}

.sedans,
.suvs,
.luxury {
  padding: 2.8125rem 3.0625rem 2.9375rem;
  max-width: 19.0625rem;
}
@media (max-width: 60em) {
  .sedans,
.suvs,
.luxury {
    max-height: 27.5rem;
    max-width: 20.3125rem;
  }
}

.btn {
  padding: 1.03125rem 2.3125rem 1.03125rem;
  border-radius: 1.5625rem;
  outline: none;
}

.btn:hover {
  border: 0.125rem solid var(--white);
  cursor: pointer;
  transform: scale(1);
}

@media (max-width: 60em) {
  .attribution {
    margin: 3.65625rem 0 1.15625rem;
  }
}